<template>
  <content-with-sidebar
    class="cws-container cws-sidebar-right blog-wrapper"
  >
    <!-- v-if="Object.keys(blogDetail).length" -->

    <!-- content -->
    <div class="blog-detail-wrapper">
      <b-row>
        <!-- blogs -->
        <b-col cols="12">
          <b-card
            :img-src="`${data.image ? logoUrl+data.image : Image}`"
            img-top
            img-alt="Blog Detail Pic"
            class="rounded-0"
            :title="data.title"
          >
            <b-media no-body>
              <b-media-aside
                vertical-align="center"
                class="mr-50"
              >
                <!-- <b-avatar
                  href="javascript:void(0)"
                  size="24"
                  :src="data.avatar" -->
                />
              </b-media-aside>
              <b-media-body>
                <small class="text-muted mr-50">by</small>
                <small>
                  <b-link class="text-body">{{ data.by }}</b-link>
                </small>
                <span class="text-muted ml-75 mr-50">|</span>
                <small class="text-muted">{{ data.createdTime }}</small>
              </b-media-body>
            </b-media>
            <!-- <div class="my-1 py-25">
              <b-link
                v-for="tag in data.tags"
                :key="tag"
              >
                <b-badge
                  pill
                  class="mr-75"
                  :variant="tagsColor(tag)"
                >
                  {{ tag }}
                </b-badge>
              </b-link>
            </div> -->
            <!-- eslint-disable vue/no-v-html -->
            <div
              class="blog-content"
              v-html="data.description"
            />

            <!-- user commnets -->
            <b-media
              v-for="user in data.UserComment"
              :key="user.avatar"
              no-body
            >
              <b-media-aside>
                <b-avatar
                  size="60"
                  :src="user.avatar"
                />
              </b-media-aside>
              <b-media-body>
                <h6 class="font-weight-bolder">
                  {{ user.fullName }}
                </h6>
                <b-card-text>
                  {{ user.comment }}
                </b-card-text>
              </b-media-body>
            </b-media>
            <!-- eslint-enable -->
            <hr class="my-2">
          </b-card>
        </b-col>
        <!--/ blogs -->
      </b-row>
      <!--/ blogs -->
    </div>
    <!--/ content -->

    <!-- sidebar -->
    <div
      slot="sidebar"
      class="blog-sidebar py-2 py-lg-0"
    >
      <!-- input search -->
      <b-form-group class="blog-search">
        <b-input-group class="input-group-merge">
          <b-form-input
            id="search-input"
            v-model="search_query"
            placeholder="Search here"
          />
          <b-input-group-append
            class="cursor-pointer"
            is-text
          >
            <feather-icon
              icon="SearchIcon"
            />
          </b-input-group-append>
        </b-input-group>
      </b-form-group>
      <!--/ input search -->

      <!-- recent posts -->
      <div class="blog-recent-posts mt-3">
        <h6 class="section-label mb-75">
          Recent Posts
        </h6>
        <b-media
          v-for="(recentpost,index) in news.slice(0,4)"
          :key="recentpost.image"
          no-body
          :class="index? 'mt-2':''"
        >
          <b-media-aside class="mr-2">
            <b-link @click="routeDetail(recentpost.slug)">
              <b-img
                :src="`${recentpost.image ? logoUrl+recentpost.image : Image}`"
                width="100"
                rounded
                height="70"
              />
            </b-link>
          </b-media-aside>
          <b-media-body>
            <h6 class="blog-recent-post-title">
              <b-link
                class="text-body-heading"
                @click="routeDetail(recentpost.slug)"
              >
                {{ recentpost.title }}
              </b-link>
            </h6>
            <span class="text-muted mb-0">
              {{ moment(recentpost.created_at).format('DD MMM, YYYY') }}
            </span>
          </b-media-body>
        </b-media>
      </div>
      <!--/ recent posts -->
    </div>
  </content-with-sidebar>
</template>

<script>
import {
  BFormInput, BMedia, BAvatar, BMediaAside, BMediaBody, BImg, BLink, BFormGroup, BInputGroup, BInputGroupAppend,
  BCard, BRow, BCol, BCardText,
} from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import { kFormatter } from '@core/utils/filter'
import ContentWithSidebar from '@core/layouts/components/content-with-sidebar/ContentWithSidebar.vue'
import moment from 'moment'
import Image from '@/assets/images/default.png'

export default {
  components: {
    BFormInput,
    BMedia,
    BAvatar,
    BMediaAside,
    BMediaBody,
    BLink,
    BCard,
    BRow,
    BCol,
    BFormGroup,
    BInputGroup,
    BInputGroupAppend,
    BImg,
    BCardText,
    ContentWithSidebar,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      logoUrl: 'https://dev.api.the-netwerk.com/storage/app/',
      Image,
      moment,
      search_query: '',
      commentCheckmark: '',
      blogDetail: [],
      socialShareIcons: [
        'GithubIcon',
        'GitlabIcon',
        'FacebookIcon',
        'TwitterIcon',
        'LinkedinIcon',
      ],
      data: {},
      news: [],
    }
  },
  watch: {
    // eslint-disable-next-line func-names
    $route() {
      this.getData()
    },
  },
  created() {
    this.getData()
  },
  mounted() {
    this.getData()
  },
  methods: {
    kFormatter,
    getData() {
      this.$http.get(`blog/${this.$route.params.id}`).then(response => {
        this.data = response.data.data
      })
      this.$http.get('blog/').then(response => {
        this.news = response.data.data
      })
    },
    routeDetail(id) {
      this.$router.replace({
        name: 'blog-detail',
        params: {
          id,
        },
      })
    },
  },
}
</script>

<style lang="scss">
